/*-----Projects App ----------*/
.todoApp {
    position: relative;
    margin-top: 20px;
    padding: 10px;
    max-width: 500px;
    min-width: 500px;
    min-height: 500px;
    border-radius: 10px;
    background: linear-gradient(135deg, #0a2a43 25%, #1b4566 50%, #0a2a43 75%);
    opacity: 0.95;
    justify-items: center;
    box-shadow: 2px 5px 10px black;
}

.todoApp h1 {
    margin-top: -30px;
    font-size: 28px;
    color: #cce7ff;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
    text-align: center;
    font-weight: bold;
    letter-spacing: 2px;
}
.todoApp h3 {
    font-size: 18px;
    color: #89c4ff; /* Немного темнее */
    text-align: center;
    font-style: italic;
    margin-bottom: 10px;
}


.todoApp li {
    font-size: 16px;
    display: flex;
    width: 100%;
    margin-bottom: 10px;
    padding: 8px;
    list-style: none;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 2px 5px black;
    border-radius: 10px;
    background-color: rgba(255, 223, 186, 0.8);
    border-left: 5px solid #e1a46b;
}

.todoApp li input {
    margin-right: 10px;
}
.todoApp li:has(input:checked) {
    text-decoration: line-through;
    background: linear-gradient(to right, #2a2a2a, #1a1a1a);
    color: #aaa;
}

.todoApp li div:nth-of-type(1) {
    /*border: 1px solid red;*/
    display: flex;
}

.todoApp li div:nth-of-type(2) {
    /*border: 1px solid blue;*/
}

.todoApp p {
    font-size: 16px;
    max-width: 100%;
}

.todoApp button {
    background: transparent;
    font-size: 16px;
    height: 30px;
    padding: 0 10px;
    border-radius: 10px;
}

.close {
    position: relative;
    top: -10px;
    left: 232px;
    min-width: 25px;
    min-height: 25px;
}

.close button {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 32px;
    height: 32px;
    background: #d9534f; /* Красный */
    color: white;
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    transition: 0.3s;
}

.close button:hover {
    background: #c9302c;
    transform: scale(1.1);
}

.todoApp form {
    display: flex;
    width: 100%;
    padding-bottom: 10px;
    justify-content: space-between;
}

.todoApp form input {
    width: 80%;
    padding: 10px;
    font-size: 16px;
    border: 2px solid #5a98d0;
    border-radius: 8px;
    background-color: rgba(255, 255, 255, 0.9);
    color: #1b4566;
    outline: none;
    transition: 0.3s;
}
input:focus {
    border-color: #89c4ff;
    box-shadow: 0 0 8px rgba(137, 196, 255, 0.8);
}


.todoApp form input::placeholder {
    color: #aaa;
}

.todoApp button {
    cursor: pointer;
}

.todoApp form button {
    padding: 8px 12px; /* Чуть меньше, чтобы не вылазило */
    height: auto;
    font-size: 14px; /* Уменьшенный текст, чтобы помещался */
    background: linear-gradient(135deg, #5a98d0, #1b4566);
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.3s;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center; /* Выравнивание по центру */
    justify-content: center; /* Центрируем текст */
    min-width: 80px; /* Чтобы не слишком сжималось */
}
.todoApp form button:hover {
    background: linear-gradient(135deg, #89c4ff, #3a6ea5);
    transform: scale(1.05);
}


.delete_button {
    min-width: 40px;
    min-height: 40px;
    padding: 0;
    color: darkred;
    border: none;
    transition: 0.2s;
}

.delete_button:hover {
    transform: scale(1.1);
}

.edit_button {
    min-width: 40px;
    min-height: 40px;
    color: saddlebrown;
    border: none;
    transition: 0.2s;
}

.edit_button:hover {
    transform: scale(1.1);
}

.todoApp li:has(input:checked) .edit_button{
    pointer-events: none;
    color: gray;
    opacity: 0.5;
}